import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute} from '@angular/router';
import { ToastController, LoadingController, AlertController, NavController } from '@ionic/angular';
import { AccessProviders } from '../../providers/access-providers';
import { Storage } from '@ionic/storage';



@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {

  email_address: string= "";
  password: string= "";

  //ADMIN
  usuarioadmin: String="";
  passadmin: String="";

 

  disabledbutton;

  constructor(
    private router: Router,
    private toastCtrl: ToastController,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private accsPrvds: AccessProviders,
    private storage: Storage,
    public navCtrl: NavController
  ) { }

  ngOnInit() {
  }

  ionViewDiEnter(){
    this.disabledbutton = false;
  }

  ///ADMIN

  async tryLoginAbmin(){
    if(this.usuarioadmin==""){
      this.presentToast('El usuario es requerido');
    } else if(this.passadmin==""){
      this.presentToast('La contrsena es requerido');
    }else{
      this.disabledbutton = true;
      const loader = await this.loadingCtrl.create({
        message: 'Prease wait.......',
      });
      await loader.present();

      return new Promise(resolve =>{
        let body = {
          aksi: 'proses_loginadim',
          usuarioadmin: this.usuarioadmin,
          passadmin: this.passadmin
        }

        this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
          if(res.success==true){
            loader.dismiss();
            this.disabledbutton = false;
            this.presentToast('Inicio de Sesión Exitoso');
            this.storage.set('storage_xxxadmin', res.result);  //storag secion creacion
            this.navCtrl.navigateRoot(['/menu-admin']);
          }else{
            loader.dismiss();
            this.disabledbutton = false;
            this.presentToast('Email or password is wrong');
          }
        },(err)=>{
          loader.dismiss();
          this.disabledbutton = false;
          this. presentAlert('Timeout');
          //this.presentToast('registro ');
          //this.navCtrl.navigateRoot(['/home']);
        });
      });
    }
   
  }

  //registro


  async tryLogin()
  {
    if(this.email_address==""){
      this.presentToast('Email is requerid');
    } else if(this.password==""){
      this.presentToast('Password is requerid');
    }else{
      this.disabledbutton = true;
      const loader = await this.loadingCtrl.create({
        message: 'Prease wait.......',
      });
      await loader.present();

      return new Promise(resolve =>{
        let body = {
          aksi: 'proses_login',
          email_address: this.email_address,
          password: this.password
        }

        this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
          if(res.success==true){
            loader.dismiss();
            this.disabledbutton = false;
            this.presentToast('Inicio de Sesión Exitoso');
            this.storage.set('storage_xxx', res.result);  //storag secion creacion
            this.navCtrl.navigateRoot(['/home']);
          }else{
            loader.dismiss();
            this.disabledbutton = false;
            this.presentToast('Email or password is wrong');
          }
        },(err)=>{
          loader.dismiss();
          this.disabledbutton = false;
          this. presentAlert('Timeout');
          //this.presentToast('registro ');
          //this.navCtrl.navigateRoot(['/home']);
        });
      });
    }
  }

  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500
    });
    toast.present();
  }


  async presentAlert(a) {
    const alert = await this.alertCtrl.create({
      header: a,
      backdropDismiss: false,
      buttons: [
        {
          text: 'Close',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Try Again',
          handler: () => {
            this.tryLogin();
          }
        }
      ]
    });
    await alert.present();
  }

  openRegister(){
    this.router.navigate(['/register']);
  }

  


}
